import { useState } from 'react';
 
import Modal from 'react-modal';

// Modal Wrapper Styles
Modal.setAppElement('#__next'); // Para prevenir alertas de acessibilidade

const ExercicioList = ({ treino }) => {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [videoUrl, setVideoUrl] = useState('');

  const exerciciosPorTreino = {
    Treino_Abdomen: [
      { nome: 'Abdominal Canivete', videoUrl: '/Treino_Abdomen/Abdominal_canivete.mp4' },
      { nome: 'Prancha', videoUrl: '/Treino_Abdomen/Prancha.mp4' },
    ],
    Treino_Bicpes: [
      { nome: 'Rosca Direta', videoUrl: '/Treino_Bicpes/Rosca_Direta.mp4' },
      { nome: 'Rosca Alternada', videoUrl: '/Treino_Bicpes/Rosca_Alternada.mp4' },
    ],
    // Outros treinos...
  };

  const exercicios = exerciciosPorTreino[treino] || [];

  const openModal = (videoUrl) => {
    setVideoUrl(videoUrl);
    setModalIsOpen(true);
  };

  const closeModal = () => {
    setModalIsOpen(false);
    setVideoUrl('');
  };

  return (
    <div className="lg:ml-64 p-6">
      <h2 className="text-2xl font-semibold mb-4">{treino}</h2>
      <ul>
        {exercicios.map((exercicio, index) => (
          <li key={index} className="mb-4 flex justify-between items-center">
            <div className="text-lg font-medium">{exercicio.nome}</div>
            <button
              onClick={() => openModal(exercicio.videoUrl)}
              className="ml-4 bg-blue-600 text-white px-4 py-2 rounded hover:bg-blue-700"
            >
              Ver Vídeo
            </button>
          </li>
        ))}
      </ul>

      {/* Modal */}
      <Modal
        isOpen={modalIsOpen}
        onRequestClose={closeModal}
        contentLabel="Vídeo do Exercício"
        className="modal-content"
        overlayClassName="modal-overlay"
      >
        <div className="relative">
          <button
            onClick={closeModal}
            className="absolute top-2 right-2 text-white text-2xl"
          >
            ×
          </button>
          <video controls className="w-full max-w-lg mx-auto mt-4">
            <source src={videoUrl} type="video/mp4" />
            Seu navegador não suporta o formato de vídeo.
          </video>
        </div>
      </Modal>
    </div>
  );
};

export default ExercicioList;
